<template>
  <div>
    <vx-header :showcancel="1" :showcomplete="1" :disabled="!isComplete" :title="$t('settings.updatePassword')" @cancel="$router.go(-1)" @complete="complete"></vx-header>
    <div class="vx-container" @scroll="scroll">
      <div class="advancedScreen-list">
        <div class="screen_name">
          <div>{{$t('selectScreenMode.deviceName')}}</div>
          <div>{{deviceName}}</div>
        </div>
        <div class="screen_address">
          <div>{{$t('selectScreenMode.deviceAddress')}}</div>
          <div>{{ip}}</div>
        </div>
      </div>
      <ul class="form">
        <li>
          <label>{{$t('settings.originalPassword')}}</label>
          <div class="flex">
            <input v-if="!showOriginalPassword" maxlength="16" type="password" v-model="originalPassword" :placeholder="$t('settings.inputOriginalPwd')" @focus="focusIndex = 0" @blur="focusIndex = -1" />
            <input v-else maxlength="16" type="text" v-model="originalPassword" :placeholder="$t('settings.inputOriginalPwd')" @focus="focusIndex = 0" @blur="focusIndex = -1" />
            <div v-show="focusIndex == 0 && originalPassword.length" class="clear" @click="originalPassword = ''"></div>
            <div class="icon" :class="{active: showOriginalPassword}" @click="showOriginalPassword = !showOriginalPassword"></div>
          </div>
        </li>
        <li>
          <label>{{$t('settings.newPassword')}}</label>
          <div class="flex">
            <input v-if="!showNewPassword" maxlength="16" type="password" v-model="newPassword" :placeholder="$t('settings.inputNewPwd')" @focus="focusIndex = 1" @blur="focusIndex = -1" />
            <input v-else maxlength="16" type="text" v-model="newPassword" :placeholder="$t('settings.inputNewPwd')" @focus="focusIndex = 1" @blur="focusIndex = -1" />
            <div v-show="focusIndex == 1 && newPassword.length" class="clear" @click="newPassword = ''"></div>
            <div class="icon" :class="{active: showNewPassword}" @click="showNewPassword = !showNewPassword"></div>
          </div>
        </li>
        <li>
          <label>{{$t('settings.confirmPassword')}}</label>
          <div class="flex">
            <input v-if="!showConfirmPassword" maxlength="16" type="password" v-model="confirmPassword" :placeholder="$t('settings.inputNewPwdAgain')" @focus="focusIndex = 2" @blur="focusIndex = -1" />
            <input v-else maxlength="16" type="text" v-model="confirmPassword" :placeholder="$t('settings.inputNewPwdAgain')" @focus="focusIndex = 2" @blur="focusIndex = -1" />
            <div v-show="focusIndex == 2 && confirmPassword.length" class="clear" @click="confirmPassword = ''"></div>
            <div class="icon" :class="{active: showConfirmPassword}" @click="showConfirmPassword = !showConfirmPassword"></div>

          </div>
        </li>
        <div class="note">{{$t('settings.passwordNote')}}</div>
      </ul>
    </div>
  </div>
</template>

<script>
  import htp from "../../api/http";
  import urls from "../../api/apiurl";
  import { setLoc, getLoc, setSen, getSen, clearLocalKey, Alert } from '../../utils';

  export default {
    data() {
      return {
        originalPassword: '',
        newPassword: '',
        confirmPassword: '',
        showOriginalPassword: false,
        showNewPassword: false,
        showConfirmPassword: false,
        focusIndex: -1,
        deviceName: '',
        ip: ''
      };
    },
    created() {
      this.deviceName = getLoc('device').deviceName;
      this.ip = getLoc('device').deviceIp;
    },
    computed: {
      isComplete() {
        return this.originalPassword.length && this.newPassword.length && this.confirmPassword.length;
      }
    },
    methods: {
      complete() {
        if(!this.isComplete) {
          return false;
        }

        if(!/[a-zA-Z0-9]{8,}/g.test(this.newPassword)) {
          Alert(this.$t('settings.passwordMistakeRules'));
          return false;
        }

        if(!/[a-zA-Z0-9]{8,}/g.test(this.confirmPassword)) {
          Alert(this.$t('settings.passwordMistakeRules'));
          return false;
        }

        if(this.confirmPassword !== this.newPassword) {
          Alert(this.$t('settings.twicePwdNotEqual'));
          return false;
        }

        htp.post(urls.alterConnectPassword, {
          oldConnectPassword: this.originalPassword,
          newConnectPassword: this.newPassword
        }).then(res => {
          if(res.status === 10000000) {
            if(res.data.status === 1) {
              window.Android.alteredPassword();
              Alert(this.$t('settings.alterPwdSuccess'));
              this.$router.go(-1);
            } else {
              Alert(this.$t('settings.oldpwMistake'));
            }
          }
        });
      },
      scroll() {
        if(document.activeElement.tagName === 'INPUT') {
          document.activeElement.blur();
        }
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-container {
    background-color: #212124;
  }
  .advancedScreen-list {
    color: #dddddd;
    background: #212124;
    font-size: 28px;
    margin-top: 25px;
    padding: 0;
  }
  .screen_name,
  .screen_address {
    height: 107px;
    line-height: 107px;
    overflow: hidden;
    background: #383940;
    display: flex;
    > div:nth-child(1) {
      width: 40%;
      padding-left: 35px;
      text-align: left;
    }
    > div:nth-child(2) {
      text-align: left;
      border-bottom: none;
    }
  }
  .screen_name {
    border-bottom: 1px solid #4f5054;
  }
  .form {
    height: 107px;
    list-style: none;
    margin-top: 25px;
    padding: 0;
    li {
      text-align: left;
      background: #383940;
      border-bottom: 1px solid #4f5054; /*no*/
      display: flex;
      height: 100%;
      label {
        width: 40%;
        padding-left: 35px;
        font-size: 28px;
        line-height: 107px;
        color: #ddd;
      }
      .flex {
        display: flex;
        justify-content: center;
        padding-right: 30px;
        flex: 1;
        input {
          width: 100%;
          align-items: center;
          background-color: inherit;
          color: #ddd;
          outline: none;
          border: none;
          font-size: 26px;
        }
        .clear {
          width: 60px;
          height: 60px;
          margin: 25px 30px 0 0;
          line-height: 107px;
          align-items: center;
          position: absolute;
          right: 60px;
          background: ~"url('../../assets/settings/clear.png') no-repeat center center/60%";
        }
        .icon {
          width: 60px;
          height: 60px;
          margin-top: 25px;
          position: absolute;
          right: 20px;
          align-items: center;
          background: ~"url('../../assets/settings/eye-hide.png') no-repeat center center/60%";
          &.active {
            background: ~"url('../../assets/settings/eye-show.png') no-repeat center center/60%";
          }
        }
      }
    }
    .note {
      padding-left: 35px;
      font-size: 22px;
      color: #a9a9a9;
      height: 73px;
      line-height: 73px;
      text-align: left;
      overflow: hidden;
      background: #383940;
    }
  }
</style>



